

.article-list {
  padding: 0;
  list-style-type: none;
}
.article-list__item {
  border-bottom: 1px #eee solid;
  padding: 0 0 10px;
}
.article-list__heading {
  margin: 0;
  padding: 10px 15px 0;
  font-size: $font-x-medium;
  a {
    text-decoration: none;
  }
}
.article-list__subtitle {
  margin: 0 0 8px 0;
}
.article-list__excerpt {
  margin: 0 0 8px 0;
}

.article-list__image {
  margin: 0 10px 8px 8px;
  @media only screen and (min-width: 320px) {
    float: right;
    margin: 15px 15px 8px 8px;
  }
}

.article-list__inner-content {
  padding: 10px 15px;
  overflow: auto;
  .article-list__heading {
    padding: 0;
    display: inline-block;
  }
}
